
.linktree{
  width: 100%;
  height: 100em;
  text-align: center;
  margin: 5px 5px;
  background: #170b516d;
}
.linktree .img{
  max-width: 130px;
  border-radius: 70px;
  margin-top: 40px;
  border: solid 2px white;
}
.linktree .b{
  background: #ffffff26;
  margin-left: 30%;
  margin-right: 30%;
  border-radius: 5px;
}
.linktree h1{
  font-size: 30px;
  margin-bottom: 10px;
  color:aliceblue;
}
.linktree ul{
  list-style: none;
  padding: 0;
}
.linktree li{
  margin: 20px 0;
}
.linktree a{
  text-decoration: none;
  color: #333;
  font-size: 25px;
  color: aliceblue;
}
.linktree .b:hover{
  background-color: #ffffff50;
}
.background{
  position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(./image/8Oh1.gif);
    background-size: cover;
    animation: moveBackground 20s linear infinite;
}
.sumir{
  display: none;
}

@media(max-width: 720px){
  .background{
    position: fixed;
      z-index: -1;
      width: 100%;
      height: 100%;
      background-image: url(./image/4Cb2.gif);
      background-size: cover;
      animation: moveBackground 20s linear infinite;
  }
  .linktree .img{
    max-width: 100px;
    border-radius: 70px;
    margin-top: 40px;
    border: double 3px rgba(255, 255, 255, 0.539);
  }
  .linktree h1{
    font-size: 20px;
    margin-bottom: 10px;
    color:aliceblue;
  }
  .linktree a{
    text-decoration: none;
    color: #333;
    font-size: 20px;
    color: aliceblue;
  }
  .linktree .b{
    background: #ffffff17;
    width: 60%;
    margin: auto;
    margin-bottom: 15px;
    padding: 5px 20px;
    border-radius: 8px;
}
body{
  overflow: hidden;
}
.linktree{
  width: 100%;
  text-align: center;
  margin: auto;
  background: #170b516d;
}
}
